body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
  background: #222222;
}

input {
  height: 30px;
  width: 250px;
  border: none;
  border-bottom: 1px solid whitesmoke;
  background: none;
  color: white;
  padding: 0 10px;
  outline: none ;
}

.enter {
  width: 100px;
  height: 30px;
  font-size: 12px;
  background: none;
  border: 1px solid whitesmoke;
  color: whitesmoke;
  margin-left: 10px;
}

.enter:hover {
  background: whitesmoke;
  color: #222222;
  cursor: pointer;
}

.todo__item {
  position: absolute;
  cursor: move;
  min-width: 215px;
  color: #222222;
  padding: 1em;
  border-radius: 5px;
}

.delete {
  position: absolute;
  right: 5px;
  top: 5px;
  background: none;
  border:none
}

.delete:hover {
  cursor: pointer;
  color: red;
  border: 1px solid rgb(255, 115, 115);
  border-radius: 5px;
}

/*  */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #222222;
}


.search__block {
  /* max-width: 1200px; */
  margin: 0 auto;
}

.search__block h1, .search__block h3 {
  color: whitesmoke;
  text-align: center;
}
.search__block h3 {
  cursor: pointer;
}

.search__block h3:hover {
  color: green;
}

.greenColor {
  color: green !important;
}

.greenYellow {
  color: yellow !important;
}

.greenGrey {
  color: gray !important;
}

.input__month__year {
  width: 250px;
  margin-left: 10px;
  font-size: 24px;
  font-family: sans-serif;
}

.showPeriod  p {
  cursor: pointer;
  color: whitesmoke;
  margin-right: 10px;
  padding-right: 5px;
  border-right: 2px solid whitesmoke;
}

.showPeriod {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  align-items: center;
  align-content: space-between;
}


.input__content {
  display: flex;
  justify-content: space-around;
}    
